<template>
	<div v-if="menurouter">
	  <div class="aaa" v-if="item.children">
		<template v-if="item.children.length == 0">
			<el-menu-item :index="item.path">
			  <i :class="item.meta.icon"></i>
			  <span slot="title" style="padding-left:4px">{{item.name}}</span>
			</el-menu-item>
		</template>

		<el-submenu v-else :index="item.path">
		  <template slot="title" >
			<i :class="item.meta.icon"></i>
			<span slot="title" style="padding-left:4px">{{item.name}}</span>
		  </template>

		  <template v-for="child in item.children" v-if="!child.hidden">
			<sidebar-item v-if="child.children&&child.children.length>0" :item="child" :key="child.path" :menurouter="menurouter"/>
			<el-menu-item v-else :key="child.path" :index="child.path">
				<i :class="child.meta.icon"></i>
			  <span slot="title" style="padding-left:4px">{{child.name}}</span>
			</el-menu-item>
		  </template>
		</el-submenu>
	  </div>
	  <el-menu-item v-else :key="item.path" :index="item.path">
		<i :class="item.meta.icon"></i>
		<span slot="title" style="padding-left:4px">{{item.name}}</span>
	  </el-menu-item>
	</div>
	<div v-else>
		<div class="bbb" v-if="item.children">
		<template v-if="item.children.length == 0">
			<el-menu-item :index="item.path" @click="menuClick(item)">
			  <i :class="item.meta.icon"></i>
			  <span slot="title" style="padding-left:4px">{{item.name}}</span>
			</el-menu-item>
		</template>

		<el-submenu v-else :index="item.path">
		  <template slot="title" >
			<i :class="item.meta.icon"></i>
			<span slot="title" style="padding-left:4px">{{item.name}}</span>
		  </template>

		  <template v-for="child in item.children" v-if="!child.hidden">
			<sidebar-item v-if="child.children&&child.children.length>0" :item="child" :key="child.path" :menurouter="menurouter"/>
			<el-menu-item v-else :key="child.path" :index="child.path" @click="menuClick(child)">
				<i :class="child.meta.icon"></i>
			  <span slot="title" style="padding-left:4px">{{child.name}}</span>
			</el-menu-item>
		  </template>
		</el-submenu>
	  </div>
	  <el-menu-item v-else :key="item.path" :index="item.path" @click="menuClick(item)">
		<i :class="item.meta.icon"></i>
		<span slot="title" style="padding-left:4px">{{item.name}}</span>
	  </el-menu-item>
	</div>
</template>

<script>
import routerConfig from '../../../config/router.config.js'

export default {
  name: 'sidebarItem',
  props: {
    item: {
      type: Object,
      required: true
    },
	menurouter: {
		type: Boolean,
		required: true
	}
  },
  methods: {
	menuClick:function(data){	
		if(data.meta.serviceName === routerConfig.serviceName){
			window.location.hash = data.path;
		}else{
			window.location.href = data.meta.url + "?token=" + localStorage.getItem('business_cache_key_5002');
		}
	}
  }
}
</script>


